import { Component, ViewChild, OnInit, AfterViewInit, ViewEncapsulation, Input, OnChanges, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { CalculatorAnalytics } from '../../services/calculator-analytics.service';
import { Subscription } from 'rxjs/Subscription';
import { FeatureAuthService, CookieService } from '../../../../../shared/services/index';
import { environment } from '../../../../../../environments/environment';
import { DateRangePickerComponent } from '../date_range_picker/date_range_picker.component';
declare var jQuery: any;
declare var Math: any;
declare var window: any;
@Component({
  selector: 'og-user-details',
  templateUrl: './user_details.component.html',
  encapsulation: ViewEncapsulation.None,
})

export class UserDetailsComponent implements OnInit, AfterViewInit, OnChanges, OnDestroy {
  @ViewChild(DateRangePickerComponent) private drPicker: DateRangePickerComponent;
  @Input() calc: any;
  avgAnswered: any = 0;
  conversionRate: any = 0;
  avgResult: any = 0;
  avgLengthTime: string;
  socialEgagement: string;
  totalTime: number = 0;
  key: any;
  mostViewedVia: string;
  mostViewedOn: string;
  engageDataTableRef: any;
  leadDataTableRef: any;
  visitorKey: string;
  limit_alert: boolean = false;
  leadLimit: Number;
  calc_id: string;
  disable: boolean = false;
  loader: Number = 1;
  postData: any = {};
  viewedOnStats: any = {
    desktop: 0,
    mobile: 0
  };

  viewedViaStats: any = {
    facebook: 0,
    twitter: 0,
    direct: 0
  };

  private subs: Subscription[] = [];
  private lead: boolean = true;

  constructor(
    private _calculatorAnalytics: CalculatorAnalytics,
    private _featureAuthService: FeatureAuthService,
    private _cookieService: CookieService,
    private cdr: ChangeDetectorRef) {
  }

  ngAfterViewInit() {
    let self = this;
    jQuery(document).on('click', '.vd', (event: any) => {
      if (this._featureAuthService.features.analytics.user_details) {
        self.visitorKey = jQuery(event.target).data('key');
        // Slide right
        event.stopPropagation();
        setTimeout(() => {
          this.cdr.detectChanges();
          jQuery('.user-detail-outer').fadeIn();
        }, 100);
      } else {
        this._featureAuthService.setSelectedFeature('analytics');
        jQuery('#premiumModal').modal('show');
        jQuery('.modal-backdrop').insertAfter('#premiumModal');
      }
    });

    jQuery(document).on('click', '.as', (event: any) => {
      this._featureAuthService.setSelectedFeature('analytics');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    });

    this.intializeDatatable();
  }

  checkLeadsLimit() {
    let storage = JSON.parse(this._cookieService.readCookie('storage'));
    let company = storage.company;
    this.limit_alert = company.current_limit.leads === -1 || company.current_limit.leads > company.current_usage.leads ? false : true;
    //this.limit_alert  = true;
    if (this.limit_alert)
      this.loader = 1;
  }

  ngOnInit() {
    this.checkLeadsLimit();
  }

  ngOnChanges() {
    if (this.calc) {
      if (this.calc.liveApp)
        this.calc_id = this.calc._id;
      else
        this.calc_id = this.calc.parentApp;
      
      let {start_date, end_date} = this.drPicker.getStartEndDates(this.calc.createdAt, new Date());
      this.postData.start_date = start_date;
      this.postData.end_date = end_date;
      
      this.intializeDatatable();
      this.loader = 0;
      this.reInitAvgVariable();
    }
  }

  calulateAnalyticsData(data: any[]) {
    if (data.length) {
      data.map((response) => {
        if (response[7] != '--' || response[9] != '--') {
          this.viewedOnStats[response[7]] += 1;
          this.viewedViaStats[response[9] == '(not set)' ? 'direct' : response[9]] += 1;
          this.mostViewedVia = Object.keys(this.viewedViaStats).reduce((a: any, b: any) => this.viewedViaStats[a] > this.viewedViaStats[b] ? a : b);
          this.mostViewedOn = Object.keys(this.viewedOnStats).reduce((a: any, b: any) => this.viewedOnStats[a] > this.viewedOnStats[b] ? a : b);
        }
      });
    } else {
      this.mostViewedVia = '--';
      this.mostViewedOn = '--';
      this.avgLengthTime = '--';
      this.socialEgagement = '--';
    }
  }

  reInitAvgVariable() {
    this.mostViewedVia = '--';
    this.mostViewedOn = '--';
    this.avgLengthTime = '--';
    this.socialEgagement = '--';
    this.avgAnswered = '--';
    this.avgResult = '--';
  }

  intializeDatatable() {
    if (this.lead.toString() == 'false') {
      this.engagementInit();
    } else {
      this.leadInit();
    }
  }

  engagementInit() {
    let self = this;
    this.engageDataTableRef = jQuery('#engagement_Table').DataTable({
      "order": [10, "desc"],
      "processing": true,
      "destroy": true,
      "serverSide": true,
      "paging": true,
      "pageLength": 10,
      "ajax": {
        "url": environment.API + "/analytic/get_engagements",
        "type": "POST",
        "data": { id: self.calc_id, start_date: this.postData.start_date, end_date: this.postData.end_date, lead: this.lead },
        "dataSrc": function (response) {
          //self.subs.push(self.avgAnalyticsData());
          self.loader = 1;
          //self.visibleEmail(response.data);
          return response.data;
        }
      },
      'sPaginationType': 'full_numbers',
      language: {
        emptyTable: '<center>No data available</center>',
        paginate: {
          "previous": '<i class="material-icons">skip_previous</i>',
          "first": '<i class="material-icons">keyboard_arrow_left</i>',
          "next": '<i class="material-icons">skip_next</i>',
          "last": '<i class="material-icons">keyboard_arrow_right</i>'
        }
      },
      columns: [
        { title: 'Location' },
        { title: 'Engagement' },
        { title: 'Questions Answered' },
        { title: (self.calc.templateType == 'Numerical') ? 'Result Obtained' : 'Outcome Obtained' },
        { title: 'Visited From' },
        { title: 'Visited On' },
        { title: 'Length of Visit' },
        { title: 'Source' },
        { title: 'Redo Count' },
        { title: '' },
        { title: 'Date' }
      ]
    });
    // this.engageDataTableRef.column(4).visible(false);
    // this.engageDataTableRef.column(5).visible(false);
    // this.engageDataTableRef.column(6).visible(false);
    // this.engageDataTableRef.column(7).visible(false);
    // this.engageDataTableRef.column(8).visible(false);
    // this.engageDataTableRef.column(9).visible(false);
    this.engageDataTableRef.column(10).visible(false);
    this.engageDataTableRef.column(4).visible(false);
    if (this.lead.toString() == 'false') {
      //
    }
    else {
      this.engageDataTableRef.column(2).visible(false);
    }
    // for ajax call
    this.engageDataTableRef.draw();
  }

  leadInit() {
    let self = this;
    this.leadDataTableRef = jQuery('#lead_Table').DataTable({
      "order": [0, "desc"],
      "processing": true,
      "destroy": true,
      "serverSide": true,
      "paging": true,
      "pageLength": 10,
      "ajax": {
        "url": environment.API + "/analytic/get_leads",
        "type": "POST",
        "data": { id: self.calc_id, start_date: this.postData.start_date, end_date: this.postData.end_date },
        "dataSrc": function (response) {
          self.updateTitles(response.columns);
          self.loader = 1;
          return response.data;
        }
      },
      'sPaginationType': 'full_numbers',
      language: {
        emptyTable: '<center>No data available</center>',
        paginate: {
          "previous": '<i class="material-icons">skip_previous</i>',
          "first": '<i class="material-icons">keyboard_arrow_left</i>',
          "next": '<i class="material-icons">skip_next</i>',
          "last": '<i class="material-icons">keyboard_arrow_right</i>'
        }
      },
      columns: [
        { title: 'Column1' },
        { title: 'Column2' },
        { title: 'Column3' },
        { title: 'Column4' },
        { title: 'Column5' },
        { title: 'Column6' },
        { title: 'Column7' },
        { title: 'Column8' },
        { title: 'Column8' },
        { title: 'Column10' },
        { title: 'Column11' },
        { title: 'Column12' },
        { title: 'Column13' },
        { title: 'Column14' },
        { title: 'Column15' }
      ]
    });
    // for ajax call
    this.leadDataTableRef.draw();
  }
  /** Update Columns Titiles */
  updateTitles(columns: any) {
    if (this.leadDataTableRef) {
      let index = 0;
      for (let column in columns) {
        // /** hide column with no data */
        if (columns[column] == 'NO_DATA' || columns[column] == 'Date')
          this.leadDataTableRef.column(index).visible(false);
        /** CHanges header title */
        jQuery(this.leadDataTableRef.column(index).header()).html(columns[column]);
        index++;
        // }
      }
    }
  }

  // visibleEmail(dataSet: any[]) {
  //   for (let data in dataSet) {
  //     console.log('datasdfsdfsd', dataSet[data][1]);
  //     if (dataSet[data][1] != 'Not Provided') {
  //       this.dataTableRef.column(1).visible(true);
  //       break;
  //     }
  //   };
  // }

  exportToGoogleSheet() {
    if (this._featureAuthService.features.analytics.spreadsheet) {
      this.disable = true;
      jQuery('.export').html('Preparing your sheet..');
      jQuery('.update').html('Updating..');
      this._calculatorAnalytics.exportToSheet({ id: this.calc_id, spredsheetUrl: this.calc.spredsheetUrl }).subscribe(
        (response: any) => {
          // if (this.calc.spredsheetUrl === '') {
          // }
          this.calc.spredsheetUrl = response;
          window.open(this.calc.spredsheetUrl, '_blank');
          this.disable = false;
          jQuery('.export').html('<i class="material-icons">exit_to_app</i> &nbsp;View Spreadsheet');
          jQuery('.update').html('<i class="material-icons">exit_to_app</i> &nbsp;View Spreadsheet');
          //  window.location.href = response;
        },
        (error) => {
          console.log('error', error);
        }

      );
    } else {
      this._featureAuthService.setSelectedFeature('analytics');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    }
  }
  avgAnalyticsData() {
    let self = this;
    return this._calculatorAnalytics.getAvgOfLeads({ id: this.calc_id, start_date: this.postData.start_date, end_date: this.postData.end_date, lead: this.lead })
      .subscribe(
      (response: any) => {
        self.avgAnswered = Math.round(response.avgAnswers);
        self.avgResult = response.avgResult ? response.avgResult.toString().replace(/<(?:.|\n)*?>|&nbsp;/gm, '').trim() : response.avgResult;
        let min = Math.floor(Number(response.avgTimeOnPage) / 60);
        let seconds = Math.round(Number(response.avgTimeOnPage) % 60);
        self.avgLengthTime = (min ? (min + ((min == 1) ? ' min ' : ' mins ')) : '') + ((seconds) ? seconds + ' s' : '');
        self.socialEgagement = response.CtaEngagementLength;
        self.conversionRate = response.conversionRate;
        self.calulateAnalyticsData(response.data);
      },
      (error: any) => {
        console.log('Something Went Wrong in Avg leads!');
      });
  }

  onDateSelect(date: any) {
    this.postData.start_date = date.start_date;
    this.postData.end_date = date.end_date;
    //refresh stats
    if (this.calc_id) {
      this.leadInit();
      this.engagementInit(); //this.subs.push(this.getLeadData(this.calc_id));
    }
  }

  leadDropdown(event: any) {
    this.lead = event;
    this.intializeDatatable();
  }

  ngOnDestroy() {
    this.unsubscribe();
  }

  unsubscribe() {
    this.subs.forEach(sub => sub.unsubscribe());
  }
}



